@import url("https://fonts.googleapis.com/css2?family=Monda&display=swap");

* {
  font-family: "Monda", sans-serif;
}

body {
  background-color: #181824;
}

.main {
  .logo__holder {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      font-size: 96px;
      line-height: 156px;
      letter-spacing: 0.1em;
      padding: 25px 40px;

      color: white;
    }

    .auth {
      font-size: 25px;
      line-height: 156px;
      letter-spacing: 0.1em;
      padding: 25px 40px;

      a {
        text-decoration: none;
        margin-right: 35px;
        color: white;

        transition: all 0.3s ease-in-out;
      }

      a:hover {
        color: #ad02fe;
      }
    }
  }

  .input__holder {
    display: flex;
    justify-content: center;

    .input {
      display: flex;
      align-items: center;
      background: #25273c;
      border-radius: 30px;
      width: 666px;

      .input__text {
        font-size: 36px;
        line-height: 58px;
        letter-spacing: 0.1em;
        color: rgba(255, 255, 255, 0.7);
        margin-right: 50px;
        padding-left: 30px;
        width: 100%;
        outline: none;
        border: none;
        background-color: transparent;
      }

      .input__btn {
        height: 69px;
        cursor: pointer;
      }
    }
  }

  .list__holder {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 60px;
    margin-bottom: 80px;

    .score {
      border: 2px solid #ad02fe;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;

      font-size: 24px;
      line-height: 39px;
      letter-spacing: 0.1em;
      padding: 10px;
      color: white;

      .right {
        a {
          text-decoration: none;
          color: white;
          transition: all 0.2s ease-in-out;
          margin-left: 25px;
          cursor: pointer;
        }

        a:hover {
          color: #ad02fe;
        }
      }
    }

    .list {
      background-color: #25273c;
      width: 666px;

      .list__item {
        display: flex;
        align-items: center;
        margin: 15px 0px;

        .text {
          font-size: 36px;
          line-height: 59px;
          letter-spacing: 0.1em;
          color: rgba(255, 255, 255, 0.7);
          margin: 0 auto;

          border-bottom: 4px solid rgba(72, 64, 64, 0.37);
        }

        .icon {
          margin: 0px 26px;
          display: flex;
          gap: 15px;

          img {
            cursor: pointer;
          }

          .faXmarkCircle {
            transition: all 0.3s ease-in-out;
            height: 46px;
            cursor: pointer;
          }

          .faXmarkCircle:hover {
            background-color: #ad02fe;
            border-radius: 100%;
          }
        }
      }
    }
  }
}
